export default {
	data() {
		return {
			isShow: false
		};
	},
	props:{
		// 弹窗标题
		title: {            // 属性名
		  type: String,     // 类型（必填），目前接受的类型包括：String, Number, Boolean, Object, Array, null（表示任意类型）
		  value: '标题'     // 属性初始值（可选），如果未指定则会根据类型选择一个
		},
		// 弹窗内容
		content: {
		  type: String,
		  value: '弹窗内容'
		},
		// 弹窗取消按钮文字
		cancelText: {
		  type: String,
		  value: '取消'
		},
		// 弹窗确认按钮文字
		confirmText: {
		  type: String,
		  value: '确定'
		}
	},
	methods:{
		_cancelEvent(){
			//第一个参数名为调用的方法名，第二个参数为需要传递的参数
			this.$emit('_cancelEvent'); 
			//console.log("left");
		},
		_confirmEvent(){
			this.$emit('_confirmEvent'); 
			//console.log("right");
		}
	}
}
